// 
const sendSms = (el, binding) => {
  el.onclick = () => {
    if (binding.value == null || binding.value == '') {
      console.warn("[]", "");
    } else {
      console.log("[]", binding.value);
      // 
      let time = 60;
      const setTime = setInterval(() => {
        el.disabled = time != 0;
        if (time == 0) {
          el.innerHTML = '';
          clearInterval(setTime);
        } else {
          el.innerHTML = time + '';
          time--;
        }
      }, 1000);
    }
  };
}

export default sendSms;
